/* styles.css */
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities'; 

@layer components {
  .btn-primary {
    @apply px-4 py-2 text-white rounded-md shadow-md ;
  }
}
.player-overlay{
  background: linear-gradient(to bottom, transparent, rgb(20, 20, 20));  
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); 
}
body{
    min-height: 100vh;
   /* background-image: url(/public/img/bg.jpg); */
    background-color: black;
    background-blend-mode: lighten;
    background-repeat: no-repeat;
    background-position: center;
    background-position-y: top;
}
.bg-modal{
  backdrop-filter: blur(5px); /* Ajusta el valor según la cantidad de desenfoque deseada */
  background: rgba(15, 14, 14, 0.8); /* Ajusta el valor de alfa para controlar la opacidad del fondo */
 }
.cryptoicons{
  max-width: 300px;
  margin:0 auto ;
}
.customContent{
  max-width: 400px;
}
.playlist{ 
  height: 140px;
  overflow-y: scroll;
}
* {
  margin: 0;
  padding: 0;
} 
h1.glitch {
  color: white;
  font-family: sans-serif;
  font-weight: 800;
  position: relative;
  font-size: 50px;  

}
  

 

@keyframes glitch-animation-1 {
  0%, 100% {
    clip: rect(random(150) + px, 350px, random(150) + px, 30px);
  }
}

@keyframes glitch-animation-2 {
  0%, 100% {
    clip: rect(random(150) + px, 350px, random(150) + px, 30px);
  }
}
